<template>
  <!-- 权限管理 -->
  <div class="authority">
    <!-- 内容 -->
    <div class="table-pannel">
      <!-- 操作栏 -->
      <div class="btn-box">
        <!-- 按钮 -->
        <div style="display: flex">
          <div class="add-btn" @click="addDialog">
            <div class="add-btn-content">
              <div class="add-btn-bg"></div>
              <div class="add-btn-name">新增</div>
            </div>
          </div>
          <div :class="[
              'edit-btn',
              { 'edit-btn-grow': multipleSelection.length > 0 },
            ]" @click="delDialog">
            <div class="add-btn-content">
              <div class="del-btn-bg"></div>
              <div class="edit-btn-name">删除</div>
            </div>
          </div>
        </div>
        <!-- 搜索 -->
        <div class="border-box-search">
          <div style="
              display: flex;
              transform: skewX(40deg);
              margin-top: 5px;
              z-index: 999;
            ">
            <div class="search-input">
              <el-input v-model="keywords" placeholder="请输入URL路径或权限名称搜索" clearable @keyup.enter.native="searchInputFun" />
            </div>
            <div class="search-btn-input" @click="searchInputFun">
              <i class="el-icon-search"></i>
              搜 索
              <!-- <div class="search-btn-bg"></div> -->
            </div>
          </div>
        </div>
      </div>
      <!-- 表格数据 -->
      <div class="flex-list">
        <div class="main-dataset animated flip" v-for="(item, index) in tableData" :key="item.id" :style="setStyle(index)">
          <div class="dataset-operate">
            <div class="dataset-edit" @click.stop="handleEdit(item)" v-show="item.fixed == 0">
              <i class="el-icon-edit"></i>
            </div>
            <div class="dataset-delete" @click.stop="handleDel(item)" v-show="item.fixed == 0">
              <i class="el-icon-delete" style="padding-left: 8px"></i>
            </div>
            <div class="dataset-delete" @click.stop="handleDetail(item)">
              <i class="el-icon-document" style="padding-left: 8px"></i>
            </div>
          </div>
          <div class="dataset-info ellipsis">
            <!-- @click.stop="activeAuthor($event,item)" -->
            <div class="info-name ellipsis" :title="item.urlName">{{ item.urlName }}</div>
            <div class="info-url" :title="item.url">{{ item.url }}</div>
            <div class="info-open">
              <template>
                <span v-if="item.open == '0'"> 私有 </span>
                <span v-else-if="item.open == '1'"> 公开 </span>
                <span v-else> 匿名 </span>
              </template>
            </div>
          </div>
          <div class="dataset-type">
            <span class="dataset-type-select" v-show="item.fixed == 0" @click.stop="selectData($event, item)"></span>
          </div>
        </div>
      </div>

      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="pageNum" background :page-size="pageSize" layout="total, prev, pager, next" :total="total">
        </el-pagination>
      </div>
    </div>

    <!-- 添加 修改 -->
    <el-dialog v-drag :title="title" :visible.sync="add" :before-close="handleClose" width="600px" append-to-body>
      <el-form ref="form" :model="addForm" :rules="addrules" label-width="80px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="权限名称" prop="urlName">
              <el-input v-model="addForm.urlName" :disabled="!edit" placeholder="请输入权限名称" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="URL路径" prop="url">
              <el-input v-model="addForm.url" :disabled="!edit" placeholder="请输入URL路径" />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row class="special-row">
          <el-col :span="24">
            <el-form-item label="是否公开" style="margin-bottom: 5px">
              <el-select v-model="addForm.open" :disabled="!edit" placeholder="请选择是否公开" style="width: 100%">
                <el-option v-for="item in dictOptions" :key="item.id.toString()" :label="item.dictName" :value="item.dictVal"></el-option>
              </el-select>
            </el-form-item>
            <ul class="desc-list">
              <li>1、私有：登录后需要为角色分配权限</li>
              <li>2、公开：登录后无需为角色分配权限</li>
              <li>3、匿名：无需登录</li>
            </ul>
          </el-col>
        </el-row>
        <el-row> </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="角色选择">
              <el-select v-model="addForm.roleInfos" placeholder="请选择角色" multiple="" :disabled="addForm.open == '1' || addForm.open == '2' || !edit" clearable style="width: 100%">
                <el-option v-for="item in roleOptions" :key="item.id.toString()" :label="item.roleName" :value="item.id.toString()"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="描述">
              <el-input v-model="addForm.description" :disabled="!edit" type="textarea" placeholder="请输入内容"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer" v-show="edit">
        <el-button type="primary" @click="addSubmit" class="confirm-btn">确 定</el-button>
        <el-button @click="addCancel">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 头部
// tab切换
// import {beginLine} from '../../plugins/canvasLine.js';
export default {
  components: {},
  data() {
    return {
      roleTabShow: "-350",
      jsId: "",
      file: null,
      questionObject: {
        // questionsNmae: '',
        file: null,
      },
      questionIds: [],
      formLabelWidth: "120px",
      fileList: [{ name: undefined }],
      templateShow: false,
      username: "",
      tel: "",
      dataStatus: "",
      pageSize: 10,
      pageNum: 0,
      pageSize2: 10,
      pageNum2: 0,
      total: 0,
      time: ["", ""],
      tableData: [],
      tableData2: [],
      multipleSelection: [],
      switchStatus: "0",
      currentPage: 5,
      add: false,
      // 表单校验
      addrules: {
        urlName: [
          { required: true, message: "权限名称不能为空", trigger: "blur" },
        ],
        url: [{ required: true, message: "URL路径不能为空", trigger: "blur" }],
      },
      addForm: {
        urlName: "",
        url: "",
        open: "0",
        description: "",
        roleInfos: [],
      },
      title: "",
      // 部门树选项
      deptOptions: undefined,
      // 性别状态字典
      //修改
      // 状态数据字典
      statusOptions: [],
      // 岗位选项
      postOptions: [],
      // 角色选项
      roleOptions: [],
      edit: false,

      editForm: {
        urlName: "",
        url: "",
        open: "0",
        description: "",
        roleInfos: [],
        id: "",
      },
      editRoleForm: {
        permissionInfos: [],
      },

      resetFlag: false,
      resetForm: {
        password: "",
      },
      editRole: false,
      activeRole: {},
      dictOptions: [],
      keywords: "",
      keywords2: "",
      edit: true,
    };
  },
  mounted() {
    // this.canvas();
    this.searchFn();
    // this.searchFn2()
    this.getRoles();
    this.queryDictByDictTypeId();
  },
  methods: {
    selectData(e, data) {
      if (data.fixed != 0) {
        return;
      }
      let classNames = e.target.parentElement.className;
      if (classNames.indexOf("dataset-type-select") == -1) {
        e.target.parentElement.className = "dataset-type dataset-type-select";
        this.multipleSelection.push(data);
      } else {
        e.target.parentElement.className = "dataset-type";
        this.multipleSelection = this.multipleSelection.filter(function (item) {
          return item.id !== data.id;
        });
      }
    },
    activeAuthor(e, item) {
      let classNames = e.currentTarget.parentElement.className;
      if (classNames.indexOf("dataset-type-active") == -1) {
        e.currentTarget.parentElement.className =
          "main-dataset animated flip dataset-type-active";
      } else {
        e.currentTarget.parentElement.className = "main-dataset animated flip";
      }
    },
    canvas() {
      //beginLine('myCanvas')
    },
    setStyle(index) {
      if (index < 10) {
        return { "animation-delay": "0." + index + "s" };
      } else {
        return { "animation-delay": index * 0.1 + "s" };
      }
    },
    handleClose() {
      this.$refs["form"].resetFields();
      this.add = false;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //点击行触发，选中或不选中复选框
    handleRowClick(row) {
      this.$refs.multipleTable.toggleRowSelection(row);
      // 获取当前选中的数据
      const _selectData = this.$refs.multipleTable.selection;
      console.log("当前选中的数据", _selectData);
      this.handleSelectionChange(_selectData);
    },
    queryDictByDictTypeId() {
      this.$get(
        $url.DATA_URL + "/gateway/queryDictByDictTypeId?dictTypeId=1"
      ).then((res) => {
        if (res.data.code == 200) {
          this.dictOptions = res.data.data;
        } else {
          this.$router.push("/");
          // this.$message({
          //   type: "error",
          //   message: res.data.msg
          // });
        }
      });
    },
    queryPermissionByRoleId() {
      this.$get(
        $url.DATA_URL +
          "/gateway/queryPermissionsByRoleId?roleId=" +
          this.activeRole.id
      ).then((res) => {
        if (res.data.code == 200) {
          this.editRoleForm.permissionInfos = [];
          for (let i = 0; i < res.data.data.length; i++) {
            this.editRoleForm.permissionInfos.push(
              res.data.data[i].id.toString()
            );
          }
        } else {
          this.$message({
            type: "error",
            message: res.data.msg,
          });
        }
      });
    },

    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleEdit(row) {
      this.title = "修改权限";
      this.add = true;
      this.edit = true;
      this.addForm.urlName = row.urlName;
      this.addForm.open = row.open.toString();
      this.addForm.url = row.url;
      this.addForm.description = row.description;
      this.addForm.roleInfos = [];
      for (let i = 0; i < row.roleInfos.length; i++) {
        this.addForm.roleInfos.push(row.roleInfos[i].id);
      }
      this.addForm.id = row.id;
    },
    handleDetail(row) {
      this.title = "权限详情";
      this.add = true;
      this.edit = false;
      this.addForm.urlName = row.urlName;
      this.addForm.open = row.open.toString();
      this.addForm.url = row.url;
      this.addForm.description = row.description;
      this.addForm.roleInfos = [];
      for (let i = 0; i < row.roleInfos.length; i++) {
        this.addForm.roleInfos.push(row.roleInfos[i].id);
      }
      this.addForm.id = row.id;
    },

    handleSizeChange(val) {
      this.pageSize = val;
      // this.getUserList()
      this.searchFn();
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      // this.getUserList()
      this.searchFn();
    },
    handleSelectChange() {},
    addDialog() {
      this.addForm = {
        urlName: "",
        url: "",
        open: "0",
        description: "",
        roleInfos: [],
      };
      this.title = "新增权限";
      this.edit = true;
      this.add = true;
      //this.getTreeselect()
    },
    getRoles() {
      this.$get($url.DATA_URL + "/gateway/queryAllRoles").then((res) => {
        if (res.data.code == 200) {
          this.roleOptions = res.data.data;
        } else {
          this.$router.push("/login");
          // this.$message({
          //   type: "error",
          //   message: res.data.msg
          // });
        }
      });
    },
    //新增确认按钮
    addSubmit() {
      if (!this.edit) {
        return;
      }
      let url = "/gateway/addPermission";
      if (this.addForm.id) {
        url = "/gateway/updatePermission";
      }
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.addForm.open == "0" && this.addForm.roleInfos.length == 0) {
            this.$message.warning("请选择角色!");
            return;
          }

          let roleArr = [];
          for (let i = 0; i < this.roleOptions.length; i++) {
            if (this.addForm.roleInfos.indexOf(this.roleOptions[i].id) != -1) {
              roleArr.push(this.roleOptions[i]);
            }
          }
          this.addForm.roleInfos = roleArr;
          this.$post($url.DATA_URL + url, this.addForm).then((res) => {
            if (res.data.code == 200) {
              this.add = false;
              this.$message({
                type: "success",
                message: this.title + res.data.msg,
              });
              // this.getUserList();
              this.searchFn();
            } else {
              this.$message({
                type: "error",
                message: res.data.msg,
              });
            }
          });
        } else {
          this.add = true;
        }
      });
    },
    addCancel() {
      this.$refs["form"].resetFields();
      this.add = false;
    },
    handleDel(row) {
      this.$confirm("此操作将永久删除该条权限信息, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.$get($url.DATA_URL + "/gateway/delPermission?id=" + row.id).then(
          (res) => {
            if (res.data.code == 200) {
              this.$message({
                type: "success",
                message: "删除成功!",
              });
              this.searchFn();
            } else {
              this.$message({
                type: "error",
                message: res.data.msg,
              });
            }
          }
        );
      });
      // this.$delete( `/onlineteaching/user/${row.id}`).then((res) => {
      //   this.getUserList()
      // })
    },
    delDialog() {
      if (this.multipleSelection.length < 1) {
        return this.$message({
          type: "error",
          message: "请至少选择一条数据",
        });
      }
      let that = this;
      this.$confirm("此操作将永久删除数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        let ids = "";
        for (let i = 0; i < that.multipleSelection.length; i++) {
          if (i == that.multipleSelection.length - 1) {
            ids += that.multipleSelection[i].id.toString();
          } else {
            ids += that.multipleSelection[i].id.toString() + ",";
          }
        }
        this.$get($url.DATA_URL + "/gateway/delPermissions?ids=" + ids).then(
          (res) => {
            if (res.data.code == 200) {
              this.$message({
                type: "success",
                message: "删除成功!",
              });
              this.multipleSelection = [];
              this.searchFn();
            } else {
              this.$message({
                type: "error",
                message: res.data.msg,
              });
            }
          }
        );
      });
    },
    searchInputFun() {
      this.pageNum = 1;
      this.searchFn();
    },

    searchFn() {
      this.multipleSelection = [];
      //   "dataset-type dataset-type-select"
      let doms = document.getElementsByClassName("dataset-type");
      for (let i = 0; i < doms.length; i++) {
        if (doms[i].className.includes("dataset-type-select")) {
          doms[i].className = "dataset-type";
        }
      }
      if (!this.cookie.getCookie("username")) {
        this.$router.push("/login");
      }
      this.$get(
        $url.DATA_URL +
          "/gateway/queryPermission?keywords=" +
          this.keywords +
          "&current=" +
          this.pageNum +
          "&size=" +
          this.pageSize
      ).then((res) => {
        if (res.data.code === 200) {
          this.tableData = res.data.data.records;
          this.total = res.data.data.total;
        } else {
          this.$message({
            type: "error",
            message: res.data.msg,
          });
          this.$router.push("/login");
        }
      });
    },
    resetFn() {
      this.username = "";
      this.tel = "";
      this.dataStatus = "";
      this.time = [];
      this.searchFn();
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/styles/mixin.scss";
.form-box {
  margin-top: 37px;
}
@font-face {
  font-family: "MyFont";
  src: url("../../../public/css/YouShe.ttf");
}
.authority {
  width: 100%;
  height: 100%;
  // background: #2b315c;
  // background: #171b22;
  @include background_color("bg-color");
  border-radius: 3px;
  // padding: 0 50px 30px 50px;
  box-sizing: border-box;
  .table-pannel {
    // background: linear-gradient(to right, #333b6c, #73859f);
    // border-radius: 10px;
    // background: url('../../assets/gif/table.gif') no-repeat;
    // background-size: 100% 100%;
    padding: 20px;
    box-sizing: border-box;
    height: calc(100% - 150px);
    margin: 0 40px;
    .btn-box {
      width: calc(100% - 50px);
      height: 34px;
      margin-left: 23px;
      display: flex;
      justify-content: space-between;
      .add-btn-content {
        transform: skewX(40deg);
        display: flex;
      }
      .add-btn {
        width: 110px;
        height: 34px;
        margin-right: 19px;
        // background: #0548a5;
        @include background_color("btn-bg");
        // border-radius: 3px;
        // border: 1px solid #0548a5;
        cursor: pointer;
        transform: skewX(-40deg);

        .add-btn-bg {
          width: 25px;
          height: 25px;
          background: url("../../assets/images/add.png") no-repeat;
          background-size: 100%;
          margin-left: 19px;
          margin-top: 5px;
          margin-right: 5px;
        }
        .add-btn-name {
          font-size: 14px;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 500;
          color: #fff;
          margin-top: 7px;
        }
      }
      .edit-btn:hover,
      .add-btn:hover {
        background: #1763af;
      }
      @keyframes glow {
        0% {
          border-color: #393;
          box-shadow: 0 0 5px rgba(0, 255, 0, 0.2),
            inset 0 0 5px rgba(0, 255, 0, 0.1), 0 1px 0 #393;
        }
        100% {
          border-color: #6f6;
          box-shadow: 0 0 20px rgba(0, 255, 0, 0.6),
            inset 0 0 10px rgba(0, 255, 0, 0.4), 0 1px 0 #6f6;
        }
      }
      .edit-btn {
        width: 110px;
        height: 34px;
        margin-right: 19px;
        // background: #0548a5;
        @include background_color("btn-bg");
        // border-radius: 3px;
        // border: 1px solid #0548a5;
        display: flex;
        cursor: pointer;
        transform: skewX(-40deg);

        .del-btn-bg {
          width: 25px;
          height: 25px;
          background: url("../../assets/images/del.png") no-repeat;
          background-size: 100%;
          margin-left: 19px;
          margin-top: 5px;
          margin-right: 5px;
        }
        .edit-btn-name {
          font-size: 14px;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 500;
          color: #fff;
          margin-top: 7px;
        }
      }
      .edit-btn-grow {
        animation: glow 800ms ease-out infinite alternate;
      }
      .del-btn {
        width: 110px;
        height: 34px;
        margin-right: 19px;
        background: #fff9f9;
        border-radius: 3px;
        border: 1px solid #ffcccc;
        display: flex;
        cursor: pointer;
        transform: skewX(-40deg);
        .del-btn-bg {
          width: 16px;
          height: 16px;
          background: url("../../assets/images/del.png") no-repeat;
          background-size: 16px 16px;
          margin-left: 19px;
          margin-top: 9px;
          margin-right: 16px;
        }
        .del-btn-name {
          font-size: 14px;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 500;
          color: #3a3f56;
          margin-top: 7px;
        }
      }
      .border-box-search {
        width: calc(100% - 130px);
        display: flex;
        flex-direction: row-reverse;
        transform: skewX(-40deg);
        border: 1px solid; //fdbb38
        border-color: #3a4659 transparent transparent #3a4659;
        position: relative;
      }
      .border-box-search::after,
      .border-box-search::before {
        position: absolute;
        left: 0px;
        top: 0;
        bottom: 0;
        right: 0;
      }
      .border-box-search::after,
      .border-box-search::before {
        content: "";
        // box-shadow:inset 0 0 0 2px #42b983;
        // animation:clipAnimate 10s linear infinite;
        // z-index: 11000
      }
      .border-box-search::before {
        animation-delay: 5s;
      }
      @keyframes clipAnimate {
        // 0% {
        //   clip:rect(200px,0,0,20px)
        // }
        // 25% {
        //   clip:rect(0,20px,0,30px)
        // }
        // 50% {
        //   clip:rect(198px,200px,200px,0)
        // }
        // 75% {
        //   clip:rect(0,200px,200px,198px)
        // }
        // 100% {
        //   clip:rect(0,200px,2px,0)
        // }
      }
      .import-btn {
        width: 110px;
        height: 34px;
        margin-right: 19px;
        background: #fbfff9;
        border-radius: 3px;
        border: 1px solid #afdc8a;
        display: flex;
        cursor: pointer;
        .import-btn-bg {
          width: 16px;
          height: 16px;
          background: url("../../assets/images/import.png") no-repeat;
          background-size: 16px 16px;
          margin-left: 19px;
          margin-top: 9px;
          margin-right: 16px;
        }
        .import-btn-name {
          font-size: 14px;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 500;
          color: #3a3f56;
          margin-top: 7px;
        }
      }
      .export-btn {
        width: 110px;
        height: 34px;
        margin-right: 19px;
        background: #fcfcfc;
        border-radius: 3px;
        border: 1px solid #d4d7df;
        display: flex;
        cursor: pointer;
        .export-btn-bg {
          width: 16px;
          height: 16px;
          background: url("../../assets/images/export.png") no-repeat;
          background-size: 16px 16px;
          margin-left: 19px;
